.game_mods_list-container {
  width: 60%;
  height: 80%;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.adds_tab {
  width: 100%;
  height: 12vh;
  background-color: #220663;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 3vh;
}
.adds_subtab {
  width: 90%;
  height: 7vh;
  background-color: #FFFFFF26;
}

.globalBody {
  width: 100vw;
  height: 87vh;
  position: relative;
  background-color: #220663;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

body {
  background: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 98vh;
  /* Запрет выделения текста для всего сайта */
  user-select: none; /* Современные браузеры */
  -webkit-user-select: none; /* Для Safari */
  -moz-user-select: none; /* Для Firefox */
  -ms-user-select: none; /* Для Internet Explorer и Edge */
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.classic_mode {
  height: 100%;
}

span {
  user-select: none;
}

#container {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  width: 40vh;
  /* 40vh */
  height: 50vh;
  border-radius: 1vh;
  background-color: white;
}

#waiting-container,
#stopped-container {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}


#waiting-counter,
#stopped-counter {
  position: absolute;
  top: -300%;
  border-radius: 50%;
  padding: 2px 5px;
  white-space: nowrap;
}

.ball {
  position: absolute;
  border-radius: 100%;
  background-color: #ff0000;
}

.element {
  position: absolute;
}

.box {
  background-color: gray;
  display: flex;
  align-items: center;
  justify-content: center;
}

.triangle {
  position: absolute;
  width: 0;
  height: 0;
}

.bonus-box {
  display: flex;
  align-items: center;
  justify-content: center;
}

.circle-bonus {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 50%;
  height: 50%;
}

.bonus-balls-circle {
  background-color: #690101;
}

.bonus-balls-circle>span {
  color: #FFFFFF;
}

.laser-circle {
  background-color: #009002;
}

.laser-circle>span {
  color: #FFFFFF;
}


.counter_container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}

.counter_container>span {
  width: 50%;
  text-align: center;
}

/* Positioning for different triangle orientations */
.triangle-RB .counter_container {
  transform: translateX(-100%);
}

/* 
.triangle-LB .counter_container {
  
} */

.triangle-RT .counter_container {
  transform: translate(-100%, -100%);
}

.triangle-LT .counter_container {
  transform: translateY(-100%);
}


.triangle-RB .counter_container>span {
  transform: translate(50%, 50%);
}

.triangle-LB .counter_container>span {
  transform: translate(-50%, 50%);
}

.triangle-RT .counter_container>span {
  transform: translate(50%, -50%);
}

.triangle-LT .counter_container>span {
  transform: translate(-50%, -50%);
}


.moving {
  transition: all .1s linear;
}

#stop-button {
  margin-top: 10px;
}

.trajectory-segment {
  position: absolute;
  border-top: 1px dashed #00ec53;
}

.bottom_game_zone_panel {
  width: 40vh;
  height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bottom_game_zone_panel-no_active {
  display: flex;
  height: 100%;
  width: 100%;
}

.bonuses_panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

.bonuses_panel-bonus {
  width: 15%;
  height: 100%;
}

.scroll_control_button {
  width: 15%;
  height: 100%;
}

.scroll_panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.laser-container {
  position: absolute;
  background-color: lightgreen;
  opacity: 0.5;
  z-index: 10;
}

.last_step_warning_zone {
  display: none;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3vh;
  background-color: #f9222276;
}

.lang-test-button {
  position: absolute;
  left: 0;
  top: 0;
}

.trail {
  position: absolute;
  background: rgba(255, 0, 0, 0.5);
  border-radius: 50%;
  pointer-events: none;
  animation: fadeOutTrail 0.5s forwards;
}

@keyframes fadeOutTrail {
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}


/* Портретный режим: контейнер занимает весь экран */
@media (orientation: portrait) {
  .globalBody {
    width: 100vw;
    height: 85vh;
  }
}

/* Альбомный режим: высота 100%, ширина 50% высоты */
@media (orientation: landscape) {
  .adds_tab {
    width: 50vh;
  }
  .globalBody {
    height: 85vh;
    width: 50vh; /* ширина равна половине высоты */
  }
}