.game_over-tab {
  position: absolute;
  left: 35%;
  top: 40%;
  width: 30%;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  z-index: 1000;
}

.rocketBox {
  background-color: #9535a6;
}
.ballDestroyerBox {
  background-color: #35a684;
}
.untouchableBox {
  background-color: #2e2e2e;
  color: #fff;
}
.magneticTrapBox {
  background-color: #203eff;
}
.active_rocket {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  transition: transform 0.2s ease;
}